import React, { useState } from 'react';
import { Search as SearchIcon, Bell } from 'lucide-react';

const Search = () => {
  const [searchQuery, setSearchQuery] = useState('');

  return (
    <div className="w-full bg-[#f5f5f5] py-3 px-4 border-b flex items-center justify-between">

      {/* LEFT SIDE TITLE */}
      <h1 className="text-2xl font-bold text-black whitespace-nowrap">
        Reported Content
      </h1>

      {/* RIGHT SIDE: SEARCH BAR + BELL */}
      <div className="flex items-center gap-4">

        {/* SEARCH INPUT */}
        <div className="relative w-[420px]">
          <input
            type="text"
            placeholder="Search By User ID"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-12 pr-4 py-3 rounded-full border border-gray-300
             shadow-sm focus:outline-none bg-white text-gray-700"
          />
          <SearchIcon className="absolute left-4 top-3.5 h-5 w-5 text-gray-600" />
        </div>

        {/* BELL ICON */}
        <div className="relative cursor-pointer">
          <Bell className="h-7 w-7 text-yellow-500" />
          <span className="absolute -top-1 -right-1 h-3 w-3 bg-red-500 rounded-full border-2 border-white" />
        </div>

      </div>
    </div>
  );
};

export default Search;
